/*
 * loaders
 */
.is-loading {
  position: relative;
  border: 0 none;
  overflow: hidden;
  z-index: 1000;
  border-radius: 0 !important;
  border: 0 none !important;
  min-height: 85px;
}

.is-loading:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(147, 167, 57, 0.06);
  top: 0;
  left: 0;
  z-index: 1000;
  -webkit-animation: .2s linear 0s 1 normal forwards running loaderOpacity;
  animation: .2s linear 0s 1 normal forwards running loaderOpacity;
}

.is-error {
  background: red;
}

.loader {
  height: 16px;
  width: 105px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader.loader--blur {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
}

.loader__wrap {
  width: auto;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.loader__circle {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #333;
  animation: move 500ms linear 0ms infinite;
  margin-right: 30px;
}

.loader__circle:first-child {
  position: absolute;
  top: 0;
  left: 0;
  animation: grow 500ms linear 0ms infinite;
}

.loader__circle:last-child {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 0;
  animation: grow 500ms linear 0s infinite reverse;
}

.loader__text {
  margin-top: 15px;
}

