  font-family: 'fontello';
  src: url('fontello/font/fontello.eot?86970883');
  src: url('fontello/font/fontello.eot?86970883#iefix') format('embedded-opentype'),
       url('fontello/font/fontello.woff2?86970883') format('woff2'),
       url('fontello/font/fontello.woff?86970883') format('woff'),
       url('fontello/font/fontello.ttf?86970883') format('truetype'),
       url('fontello/font/fontello.svg?86970883#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
