

//!
//edia min
//@include mediamin($screen-xs)
//@include mediamin($screen-sm)
//@include mediamin($screen-md)
//@include mediamin($screen-lg)

@mixin mediamin($breakpoint) {

	@media (min-width: $breakpoint) {
		@content; } }


//!
//edia max
//@include mediamax($screen-xs-max)
//@include mediamax($screen-sm-max)
//@include mediamax($screen-md-max)
//@include mediamax($screen-lg-max)

@mixin mediamax($breakpoint) {

	@media (max-width: $breakpoint) {
		@content; } }


//!
//ink
//@include link(red, blue)

@mixin link($link, $hover) {

	& {
		color: $link;

		&:hover {
			color: $hover; } } }


//!
//ransition
//@include transition()
//@include transition(all, 300ms, $function)
//@include transition(all, 300ms, linear)
//// [ease, linear, ease-in, ease-out, ease-in-out]
@mixin transition($p: all, $d: 300ms, $t: linear) {
	transition: $p $d $t; }
