
$font-family: map-get($fonts, 'family');
$font-color: map-get($fonts, 'color');
$font-size: map-get($fonts, 'size');

html, body {
    color: map-get($colors, first);
    background-color: map-get($font-color, white);
    font-family: map-get($font-family, first), sans-serif;
    font-size: map-get($font-size, l);
    line-height: 1.5;
    font-style: normal;
    font-weight: 300;

    @include media-breakpoint-up(md) {
        font-size: map-get($font-size, m); } }

body {
    min-height: 100%;
    display: -ms-flexbox;
    -ms-flex-direction: column; }

p {
    color: map-get($colors, first);
    font-size: map-get($font-size, l);
    margin-top: 15px;
    margin-bottom: 15px;
    line-height: 1.42;

    @include media-breakpoint-up(md) {
        font-size: map-get($font-size, m); } }

a {
    color: map-get($colors, first); }

.wrapper {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column; }

.page {
    flex-grow: 1; }

.section {
    position: relative;

    &:first-child {
        padding-top: 80px; }

    &.section--page-top {
        min-height: 300px; }

    &__title {
        font-family: map-get($font-family, second), sans-serif;
        font-size: map-get($font-size, xxxl);
        margin: 0 0 20px; }

    &__lead {
        font-size: map-get($font-size, l);
        margin: 0; } }

.tagline {
    &:after {
        content: "";
        display: block;
        margin: 20px auto;
        width: 70px;
        height: 0;
        border-bottom: 2px solid #eceff1; } }

.header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    background-color: #fff;
    padding: 10px 0;
    box-shadow: 3px 0px 15px 0px rgba(0, 0, 0, 0.05); }

.footer {
    position: absolute;
    bottom: 5px;
    text-align: right;
    right: 5px;
    font-size: 14px; }

.logo {
    width: 100%;
    margin: 0;
    display: block; }

.btn {
    border-radius: 2px; }


.xxxx {
    min-height: 547px !important; }

.list__item {
    margin: 10px;
    background: #ccc; }

.list__header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    min-height: 45px; }

.list__footer {
    margin-top: 1px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    background: color(gray_superlight);
    min-height: 45px; }

